import React from 'react';
import ImageUploader from 'react-images-upload';
import { Button } from 'react-materialize';

// 5 MB
const MAX_FILE_SIZE = 5242880;

class BulkView extends React.Component {
  constructor(props) {
    super(props);

	this.state = {
	  pictures: [],
	  uploaderKey: 0
	};
  }

  // "picture" is an array of file objects
  onDrop = picture => {
	this.setState({
	  pictures: this.state.pictures.concat(picture),
	  uploaderKey: this.state.uploaderKey + 1
	});

	// Key property given in ImageUploader; this prevents the previously uploaded values in "picture" argument
  };

  handleRemove = async (e, item) => {
	await this.setState({
	  pictures: this.state.pictures.filter(pic => pic !== item),
	  uploaderKey: this.state.uploaderKey + 1
	});
  };

  handleSubmit = () => {
	const data = {
	  "dates": document.getElementsByClassName("date-input"),
	  "captions": document.getElementsByClassName("desc-input"),
	  "pictures": this.state.pictures
	};

	this.props.sendData(data);
  };

  render() {
	return (
	  <div>
		  <ImageUploader
		    withIcon={true}
			buttonText="Choose Images"
			onChange={this.onDrop}
			imgExtension={[".jpg", ".jpeg", ".gif", ".png", ".tif", ".tiff"]}
			maxFileSize={MAX_FILE_SIZE}
			key={this.state.uploaderKey}
		  />
		  { this.state.pictures.length === 0 ? null :
		    this.state.pictures.map((item, index) => (
			  <div className="row" key={index}>
			  <div className="col l1">
			   <Button onClick={e => this.handleRemove(e, item)} style={{marginTop: "100%"}} className="btn-floating red waves-effect" ><i className="material-icons">remove</i></Button> 
			  </div>
			  <div className="col l5">
			  <img alt="#!" style={{maxWidth: "50%", maxHeight: "50%"}} src={URL.createObjectURL(item)} />
			  </div>
			  
			  <div className="col l6">
            <div className="input-field col s12">
              <input
                id={index + " date"}
                type="text"
				className="date-input"
              />
              <label htmlFor="date">Date</label>
            </div>
            <div className="input-field col s12">
              <input
                id={index + " desc"}
                type="text"
				className="desc-input"
              />
              <label htmlFor="desc">Caption</label>
            </div>
			  </div>
			  </div>
			))
		  }
		  <Button className="waves-effect" onClick={this.handleSubmit} >Submit</Button>
	  </div>
	);
  }
};

export default BulkView;
