import React from 'react';

const buttonStyle = {
  width: "100%", 
  textTransform: "none", 
  background: "#ee6e73"
};

class DisplayAlbum extends React.Component {
  handleClick = (e, albumName) => {
	this.props.handleSetAlbum(albumName);
  };

  render() {
    return (
	  <div>
	    <div 
		  id="display-albums" 
		  className="modal"
          style={{
            marginTop: "30%",
            paddingTop: "20%",
            backgroundColor: "rgb(255, 255, 255, 0.95",
          }}
		>
          <div className="modal-content" style={{width: "100%"}}>
            <a
              href="#!"
              className="modal-close waves-effect"
              style={{ display: "inline-block" }}
            >
            Close
            </a>
            <h4>Display Albums</h4>
			<div className="row">
			  <a style={buttonStyle} onClick={e => this.handleClick(e, "default")} href="#!" className="waves-effect btn">Default</a>
			</div>
			{
			  this.props.albums !== undefined ?
				  this.props.albums.map(item => (
					<div className="row">
					  <a style={buttonStyle} onClick={e => this.handleClick(e, item)} href="#!" className="waves-effect btn">{item}</a>
					</div>
				))
				: null
			}
          </div>
        </div>
	  </div>
	);
  }
}

export default DisplayAlbum;
