import M from "materialize-css";
import React from "react";
import axios from "axios";

const dropdownOptions = {
  coverTrigger: false
};

class DeleteAlbum extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.deleteAlbumRef = React.createRef();
  }

  componentDidMount() {
	const elems = document.getElementById('delete-album');
    M.Dropdown.init(elems, dropdownOptions);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.myRef.current.scrollTo(0, 0);
  }

  handleSubmit = () => {
    const helperStatus = this.deleteAlbumRef.current;

    let request = {
      idtoken: this.props.token,
      albumName: this.deleteAlbumRef.current.value
    };

    if (helperStatus.classList.contains("valid")) {
      helperStatus.classList.remove("valid");
    }

    if (helperStatus.classList.contains("invalid")) {
      helperStatus.classList.remove("invalid");
    }

	const callUpdate = this.props.updateList;

    axios
      .post("/deleteAlbum", request)
      .then(function (response) {
        helperStatus.classList.toggle("valid");
		callUpdate();
        console.log(response);
      })
      .catch(function (error) {
        helperStatus.classList.toggle("invalid");
        console.log(error);
      });
  };

  handleKeyDown = (e) => {
    if (e.key !== "Enter") {
      this.deleteAlbumRef.current.classList.remove("valid");
      this.deleteAlbumRef.current.classList.remove("invalid");
    } else {
      this.handleSubmit();
    }
  };

  render() {
    return (
      <div
        ref={this.myRef}
        id="delete-modal"
        className="modal"
        style={{
          marginTop: "30%",
          paddingTop: "20%",
          backgroundColor: "rgb(255, 255, 255, 0.95",
        }}
      >
        <div className="modal-content" style={{width: "100%"}}>
          <a
            href="#!"
            className="modal-close waves-effect"
            style={{ display: "inline-block" }}
          >
            Close
          </a>
          <h4>Delete Album</h4>
          <div className="row">
            <div className="input-field col s12">
              <input
                ref={this.deleteAlbumRef}
                id="albumNameToDelete"
                type="text"
                placeholder="e.g. Memories"
                onKeyDown={this.handleKeyDown}
              />
              <label htmlFor="albumName">Album Name</label>
              <span
                className="helper-text"
                data-error="Album does not exist."
                data-success="Album was deleted."
              ></span>
            </div>
          </div>
          <a
		    id="delete-album"
            href="#!"
            className="dropdown-trigger waves-effect waves-light red btn"
			data-target="delete-dropdown"
          >
            <i className="material-icons left">delete</i>Submit
          </a>
		  <ul id="delete-dropdown" className="dropdown-content">
		    <li><a href="#!" onClick={this.handleSubmit} >Confirm Delete</a></li>
		  </ul>
        </div>
      </div>
    );
  }
}

export default DeleteAlbum;

