import React from 'react';
import BulkView from './BulkView';
import M from "materialize-css";
import BeatLoader from "react-spinners/BeatLoader";
import axios from 'axios';

const headersConfig = {
  "Content-Type": "multipart/form-data",
};

class AddPhotosBulk extends React.Component {
  constructor(props) {
	super(props);

	this.state = {
	  selectedAlbum: "",
	  message: "",
	  loading: false
	};
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
	if (prevProps.albumsList !== this.props.albumsList) {
	  const elems = document.querySelectorAll("select");
	  M.FormSelect.init(elems);
	}
  }

  handleSelect = e => {
	this.setState({
	  selectedAlbum: e.target.value,
	  message: ""
	});
  };

  sendData = async (data) => {
	this.setState({
	  message: ""			
	});

	if (this.state.selectedAlbum === "") {
	  this.setState({
	    message: "Please make sure an album is selected."
	  });

	  return;
	}

	if (data.pictures.length === 0) {
	  await this.setState({
  		message: "Please add some photos before uploading them!"	
	  });

	  return;
	}

	await this.setState({
	  loading: true
	});

	for (let i = 0; i < data.pictures.length; ++i) {
	  let bodyFormData = new FormData();
      bodyFormData.append("idtoken", this.props.idtoken);
      bodyFormData.append("albumName", this.state.selectedAlbum);
      bodyFormData.append("caption", data.captions[i].value);
      bodyFormData.append("date", data.dates[i].value);
      bodyFormData.append("image", data.pictures[i]);

      const result = await axios
		  .post("/addPhoto", bodyFormData, { headers: headersConfig })
		  .then( async (response) => {
		    return 200;
		  }, error => {
			if (error.response.status === 403) {
			  this.setState({
				message: "Could not add photo! Album does not exist.",
			  });
			} else if (error.response.status === 400) {
			  this.setState({
				message: "Could not add photo! Try relogging into your account.",
			  });
			} else if (error.response.status === 404) {
			  this.setState({
				message: "Please connect to the internet to use this feature.",
			  });
			}
			return error.response.status;
		  });

		if (result !== 200) {
		  this.setState({
		    loading: false
		  });

		  return;
		}
	}

	this.setState({
	  loading: false,
	  message: "Successfully added photos to " + this.state.selectedAlbum + " album!"
	});

	// Refresh the current album
	this.props.setAlbumCallBack(this.props.album);
  };

  render() {
    return (
	  <div>
	    <div id="bulk" className="modal arg">
          <div className="modal-content">
            <h4>Add Photos</h4>
          <div className="row">
            <div className="input-field col s12">
              <select
                defaultValue={this.state.selectedAlbum}
                onChange={this.handleSelect}
              >
                <option value="" disabled>
                  Choose Album
                </option>
                {this.props.albumsList ? this.props.albumsList.map((item) => (
                  <option key={item} value={item}>{item}</option>
                )) : null}
              </select>
            </div>
		  </div>
		  	<BulkView sendData={this.sendData}/>
			      <div className="sweet-loading">
        <BeatLoader
          size={10}
          color={"#123abc"}
          loading={this.state.loading}
        />
		  <div className="row">
            <span className="helper-text">{this.state.message}</span>
		  </div>
      </div>
          </div>
    	  <div className="modal-footer">
            <a href="#!" className="modal-close waves-effect waves-green btn-flat">Exit</a>
    	  </div>
  	    </div>
	  </div>
	);
  }
}

export default AddPhotosBulk;
