import React from "react";
import axios from "axios";

class AddAlbum extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.createAlbumRef = React.createRef();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.myRef.current.scrollTo(0, 0);
  }

  handleSubmit = () => {
    const helperStatus = this.createAlbumRef.current;

    let request = {
      idtoken: this.props.token,
      email: this.props.email,
      albumName: this.createAlbumRef.current.value,
    };

    if (helperStatus.classList.contains("valid")) {
      helperStatus.classList.remove("valid");
    }

    if (helperStatus.classList.contains("invalid")) {
      helperStatus.classList.remove("invalid");
    }

	const callUpdate = this.props.updateList;

    axios
      .post("/createAlbum", request)
      .then(function (response) {
        helperStatus.classList.toggle("valid");
		callUpdate();
        console.log(response);
      })
      .catch(function (error) {
        helperStatus.classList.toggle("invalid");
        console.log(error);
      });
  };

  handleKeyDown = (e) => {
    if (e.key !== "Enter") {
      this.createAlbumRef.current.classList.remove("valid");
      this.createAlbumRef.current.classList.remove("invalid");
    } else {
      this.handleSubmit();
    }
  };

  render() {
    return (
      <div
        ref={this.myRef}
        id="modal2"
        className="modal"
        style={{
          marginTop: "30%",
          paddingTop: "20%",
          backgroundColor: "rgb(255, 255, 255, 0.95",
        }}
      >
        <div className="modal-content" style={{width: "100%"}}>
          <a
            href="#!"
            className="modal-close waves-effect"
            style={{ display: "inline-block" }}
          >
            Close
          </a>
          <h4>Add Album</h4>
          <div className="row">
            <div className="input-field col s12">
              <input
                ref={this.createAlbumRef}
                id="albumName"
                type="text"
                placeholder="e.g. Memories"
                onKeyDown={this.handleKeyDown}
              />
              <label htmlFor="albumName">Album Name</label>
              <span
                className="helper-text"
                data-error="Album exists / is invalid."
                data-success="Album created!"
              ></span>
            </div>
          </div>
          <a
            href="#!"
            onClick={this.handleSubmit}
            className="waves-effect waves-light btn"
          >
            <i className="material-icons left">cloud</i>Submit
          </a>
        </div>
      </div>
    );
  }
}

export default AddAlbum;

