import React from "react";
import M from "materialize-css";
import axios from "axios";
import office from "./images/office.jpg";

import { Editor } from "@tinymce/tinymce-react";

// Getting item means getting a string, so we have to parse the data to get a Javascript object
const initial = sessionStorage.getItem("content");

const onSave = (content) => {
  //console.log("saving", content);
  sessionStorage.setItem("content", content);
};

const options = {
  coverTrigger: false,
};

class Cooking extends React.Component {
  constructor(props) {
    super(props);
    this.recipeNameRef = React.createRef();
    this.editorRef = React.createRef();
	this.submitRef = React.createRef();
    this.state = {
      addRecipeMessage: "",
      recipes: [],
      content: "",
    };
  }

  handleEditorChange = (e) => {
    onSave(e.target.getContent());
    //console.log("Content was updated:", e.target.getContent());

    this.setState({
      content: e.target.getContent(),
    });
  };

  componentDidUpdate(prevProps, prevState, snapshot) {
	if (this.props.idtoken !== "") {
	  this.submitRef.current.classList.remove("disabled");
	  document.querySelectorAll(".recipes").forEach(e => e.classList.remove("disabled"));
	  document.querySelectorAll(".edit-recipes").forEach(e => e.classList.remove("disabled"));
	} else {
	  this.submitRef.current.classList.add("disabled");
	  document.querySelectorAll(".recipes").forEach(e => e.classList.add("disabled"));
	  document.querySelectorAll(".edit-recipes").forEach(e => e.classList.add("disabled"));
	}

    const elems = document.querySelectorAll(".collapsible");
    M.Collapsible.init(elems);

	const dropdowns = document.querySelectorAll(".recipes");
    M.Dropdown.init(dropdowns, options);
  }

  componentDidMount() {
    // In case of saved data from local storage
    this.setState({
      content: initial,
    });

    const elems = document.querySelectorAll(".collapsible");
    M.Collapsible.init(elems);

	const dropdowns = document.querySelectorAll(".recipes");
    M.Dropdown.init(dropdowns, options);

	const isLoggedIn = this.props.idtoken === "" ? false : true;

    axios
      .get("/getAllRecipes", { params: { idtoken: this.props.idtoken } })
      .then(
        (response) => {
          this.setState({
            recipes: response,
          });

		  // Can only be applied after the dropdowns are initialized
		  if (!isLoggedIn) {
	        document.querySelectorAll(".recipes").forEach(e => e.classList.add("disabled"));
	        document.querySelectorAll(".edit-recipes").forEach(e => e.classList.add("disabled"));
		  }
        },
        (error) => {}
      );
  }

  handleSubmit = () => {
    const recipeName = this.recipeNameRef.current.value;

    if (recipeName === "") {
      this.recipeNameRef.current.classList.add("invalid");
      this.setState({
        addRecipeMessage: "Please enter a recipe name.",
      });
      return;
    }

    this.recipeNameRef.current.classList.remove("valid");
    this.recipeNameRef.current.classList.remove("invalid");

    const request = {
      recipeName: recipeName,
      data: this.state.content,
      idtoken: this.props.idtoken,
    };

    axios.post("/addRecipe", request).then(
      (response) => {
        this.recipeNameRef.current.classList.add("valid");
		//console.log("added recipe!");

        axios
          .get("/getAllRecipes", { params: { idtoken: this.props.idtoken } })
          .then(
            (response) => {
			  //console.log("refreshing recipes list");
              this.setState({
                recipes: response,
              });
            },
            (error) => {}
          );
      },
      (err) => {
        this.recipeNameRef.current.classList.add("invalid");

        if (err.response.status === 404) {
          this.setState({
            addRecipeMessage:
              "Please connect to the internet to use this feature.",
          });
        } else if (err.response.status === 400) {
          this.setState({
            addRecipeMessage:
              "Account login status expired. Please logout and login.",
          });
        } else if (err.response.status === 500) {
          this.setState({
            addRecipeMessage: "Recipe name already exists.",
          });
        } else if (err.response.status === 403) {
          this.setState({
            addRecipeMessage: "Only admins are authorized to add a recipe.",
          });
        }
      }
    );
  };

  handleKeydown = (e) => {
    this.recipeNameRef.current.classList.remove("valid");
    this.recipeNameRef.current.classList.remove("invalid");
  };

  handleDelete = (e, recipeName) => {
    const request = {
      recipeName: recipeName,
      idtoken: this.props.idtoken
    };

    //console.log(request);

    axios.post("/deleteRecipe", request).then(
      (response) => {
		axios
		  .get("/getAllRecipes", { params: { idtoken: this.props.idtoken } })
		  .then(
			(response) => {
			  this.setState({
				recipes: response,
			  });
		    },
			(error) => {}
		    );
      },
      (error) => {
        console.log(error);
      }
    );
  };

  render() {
    let recipesToRender = [];

    if (this.state.recipes.length !== 0) {
      this.state.recipes.data.forEach((recipe) => {
        recipesToRender.push(recipe);
      });
    }

    return (
      <div className="row">
        <ul className="collapsible popout">
          <li>
            <div className="collapsible-header">
              <i className="material-icons">playlist_add</i>Add Recipe
            </div>
            <div className="collapsible-body">
              <Editor
                ref={this.editorRef}
                initialValue={initial || null}
                apiKey="p234ptga2jzgn5mia4hnruilh2zzuhfchjd7ja98rwu5p6g4"
                init={{
				  contextmenu: false,
				  browser_spellcheck : true,
                  height: 500,
				  selector: "textarea",
                  plugins: [
                    "advlist autolink lists link image",
                    "charmap print preview anchor help",
                    "searchreplace visualblocks code",
                    "insertdatetime media table paste wordcount",
                  ],
                  toolbar:
                    "undo redo | fontselect | fontsize | forecolor | backcolor | formatselect | bold italic | alignleft aligncenter alignright | bullist numlist outdent indent | help",
				  paste_enable_default_filters: false,
				  paste_word_valid_elements: "b,strong,i,em,h1,h2,h3,h4,h5"
                }}
                onChange={this.handleEditorChange}
              />
              <div className="row">
                <div className="col s3"></div>
                <div className="input-field col s6">
                  <input
                    ref={this.recipeNameRef}
                    id="recipename"
                    type="text"
                    onKeyDown={this.handleKeydown}
                  />
                  <label htmlFor="recipename">Recipe Name</label>
                  <span
                    className="helper-text"
                    data-error={this.state.addRecipeMessage}
                    data-success="Recipe created!"
                  ></span>
                </div>
                <div className="col s3"></div>
              </div>
              <button
                className="btn waves-effect waves-light"
                type="submit"
                name="action"
                onClick={this.handleSubmit}
				ref={this.submitRef}
              >
                Submit
                <i className="material-icons right">send</i>
              </button>
            </div>
          </li>
          {recipesToRender.map((item) => (
            <li>
              <div className="collapsible-header">
                <i className="material-icons">menu_book</i>
                {item.name}
              </div>
              <div
                className="collapsible-body instructions"
              >
			    <div className="row">
			    <a className="dropdown-trigger btn right red recipes" href="#!" data-target={item.name} >
			      Delete Recipe
			    </a>
			    <a className="btn right waves-effect edit-recipes" href="#!" data-target={item.name} onClick={e => this.props.triggerEdit(e, item.data, item.name)}>
			      Edit Recipe
			    </a>
				</div>
			    <ul id={item.name} className="dropdown-content">
			      <li>
				    <a href="#!" onClick={e => this.handleDelete(e, item.name)}>Confirm</a>
				  </li>
			    </ul> 
			    <div className="recipe-display" dangerouslySetInnerHTML={{__html: item.data}} ></div>
			  </div>
            </li>
          ))}
        </ul>
		<div className="row">
		  <div className="col s4">
			  <div class="card">
    <div class="card-image waves-effect waves-block waves-light">
      <img class="activator" src={office}/>
    </div>
    <div class="card-content">
      <span class="card-title activator grey-text text-darken-4">Card Title<i class="material-icons right">more_vert</i></span>
      <p><a href="#">This is a link</a></p>
    </div>
    <div class="card-reveal">
      <span class="card-title grey-text text-darken-4">Card Title<i class="material-icons right">close</i></span>
      <p>Here is some more information about this product that is only revealed once clicked on.</p>
    </div>
  </div>
		  </div>
		</div>
      </div>
    );
  }
}

export default Cooking;

