import React from "react";
import { Button } from "react-materialize";
import { Editor } from "@tinymce/tinymce-react";
import axios from "axios";

class EditRecipe extends React.Component {
  constructor(props) {
    super(props);
    this.editorRef = React.createRef();
  }

  handleSubmit = async () => {
    const request = {
      idtoken: this.props.idtoken,
      recipeName: this.props.recipeName,
      data: this.editorRef.current.editor.getContent(),
    };

    await axios.post("/updateRecipe", request).then(
      (response) => {
        this.props.recipeEditCallback();
      },
      (error) => {
        console.log("Unsuccessful update");
      }
    );
  };

  render() {
    const data = this.props.recipeToEdit;

    return (
      <div id="edit-recipe" className="modal arg">
        <div className="modal-content">
          <h4>Edit Recipe</h4>
          <Editor
            key={data}
            ref={this.editorRef}
            value={data}
            apiKey="p234ptga2jzgn5mia4hnruilh2zzuhfchjd7ja98rwu5p6g4"
            init={{
              contextmenu: false,
              browser_spellcheck: true,
              height: 500,
              selector: "textarea",
              plugins: [
                "advlist autolink lists link image",
                "charmap print preview anchor help",
                "searchreplace visualblocks code",
                "insertdatetime media table paste wordcount",
              ],
              toolbar:
                "undo redo | fontselect | fontsize | forecolor | backcolor | formatselect | bold italic | alignleft aligncenter alignright | bullist numlist outdent indent | help",
              paste_enable_default_filters: false,
              paste_word_valid_elements: "b,strong,i,em,h1,h2,h3,h4,h5",
            }}
          />
          <br />
          <Button waves="light" onClick={this.handleSubmit}>
            Submit Changes
          </Button>
        </div>
        <div className="modal-footer">
          <a
            href="#!"
            className="modal-close waves-effect waves-green btn-flat"
          >
            Exit
          </a>
        </div>
      </div>
    );
  }
}

export default EditRecipe;

