import React, { Component } from "react";
import "./styles/App.css";
import MenuBar from "./MenuBar.js";
import PhotoCarousel from "./PhotoCarousel.js";
import NavBar from "./NavBar.js";
import Home from "./Home.js";
import Cooking from "./Cooking.js";
import AddPhotosBulk from "./AddPhotosBulk.js";
import EditRecipe from "./EditRecipe.js";
import M from "materialize-css";
import "materialize-css/dist/css/materialize.min.css";
import "./styles/index.css";
import "./Modal.css";
import axios from "axios";

export const DEFAULT_ALBUM = "default";
export const SEARCH_FLAGS = [
  "Please login to use this feature.",
  "No album was found with any photos.",
  "Make sure you're connected to the internet.",
];
const Tab = {
  HOME: 1,
  PHOTOS: 2,
  COOKING: 3,
};
const TabNames = ["Photos", "Cooking", "Home"];

// 55 minutes per refreshing auth token
const REFRESH_TIME = 3300000;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      idtoken: "",
      email: "",
      currAlbum: DEFAULT_ALBUM,
      photos: [],
      searchStatus: "",
      currentTab: Tab.HOME,
      pageName: TabNames[2],
	  recipeToEdit: "",
	  recipeName: "",
	  editModal: null,
	  forceKey: 0,
	  albumsList: [],
	  googleUser: null
    };

	this.menuRef = React.createRef();
  }

  // Gets executed even if user refreshes the page because
  // the onSuccess callback in NavBar updates the googleUser
  componentDidUpdate() {
	const googleUser = this.state.googleUser;

	this.interval = setInterval(() => {
	  if (googleUser !== null) {
	    googleUser.reloadAuthResponse().then(resolve => {
		  this.setState({
		    idtoken: resolve.id_token
		  });
		}, error => {
		  console.log(error);	
		});
	  }
	}, REFRESH_TIME);
  }

  authCallBack = (data) => {
    this.setState({
      idtoken: data.idtoken,
      email: data.email,
	  googleUser: data.googleUser,
      searchStatus: "",
    });
  };

  removeAuthCallBack = () => {
    this.setState({
      idtoken: "",
      email: "",
      searchStatus: "",
    });
  };

  setAlbumCallBack = async (album) => {
    await axios
      .get("/albumPhotos", {
        params: { idtoken: this.state.idtoken, albumName: album },
      })
      .then(
        (resolve) => {
          if (
            resolve.data[0] &&
            resolve.data[0].album &&
            resolve.data[0].album.length !== 0
          ) {
            this.setState({
              currAlbum: album,
              photos: resolve.data[0].album,
              searchStatus: "",
            });
          } else {
            this.setState({
              currAlbum: DEFAULT_ALBUM,
              photos: [],
              searchStatus: SEARCH_FLAGS[1],
            });
          }
        },
        (error) => {
          if (error.response.status === 400) {
            this.setState({
              searchStatus: SEARCH_FLAGS[0],
            });
          } else if (error.response.status === 404) {
            this.setState({
              searchStatus: SEARCH_FLAGS[2],
            });
          }

          //console.log("Album doesn't exist!");
        }
      );

	await this.setState({
	  currentTab: Tab.PHOTOS
	});

	// Manually update the active window
	this.menuRef.current.photosRef.current.click();
  };

  clearCallback = () => {
    this.setState({
      searchStatus: "",
    });
  };

  handleHome = () => {
    this.setState({
      currentTab: Tab.HOME,
      pageName: TabNames[2],
    });
  };

  menuBarCallBack = (tab) => {
    let name = null;

    if (tab === TabNames[0]) {
      tab = Tab.PHOTOS;
      name = 0;
    } else if (tab === TabNames[1]) {
      tab = Tab.COOKING;
      name = 1;
    } else if (tab === TabNames[2]) {
      tab = Tab.HOME;
	  name = 2;
	}

    this.setState({
      currentTab: tab,
      pageName: TabNames[name],
    });
  };

  triggerEdit = async (e, recipeData, recipeName) => {
	const elem = document.getElementById("edit-recipe");
    const modal = M.Modal.init(elem);

	await this.setState({
	  recipeToEdit: recipeData,
	  recipeName: recipeName,
	  editModal: modal
	});

	modal.open();
  };

  recipeEditCallback = async() => {
	await axios
      .get("/getAllRecipes", { params: { idtoken: this.props.idtoken } })
      .then(
        (response) => {
          this.setState({
		    forceKey: this.state.forceKey + 1
          });
        },
        (error) => {}
      );

	this.state.editModal.close();
  };

  triggerBulk = async (albumsList) => {
	const elem = document.getElementById("bulk");
    const modal = M.Modal.init(elem);

	await this.setState({
	  albumsList: albumsList
	});

	modal.open();
  };

  render() {
    return (
      <div className="App" style={{ background: "#FFFAFA" }}>
        <NavBar
          getAuth={this.authCallBack}
          removeAuthCallBack={this.removeAuthCallBack}
		  setAlbumCallBack={this.setAlbumCallBack}
		  album={this.state.currAlbum}
		  triggerBulk={this.triggerBulk}
        />
        <MenuBar menuBarCallBack={this.menuBarCallBack} ref={this.menuRef}/>
        {this.state.currentTab === Tab.PHOTOS ? (
		  <div>
            <div className="row">
		    <div className="col m1 l3"></div>
		    <div className="col s12 l6 m10">
            <PhotoCarousel
              setAlbum={this.setAlbumCallBack}
              idtoken={this.state.idtoken}
              email={this.state.email}
              album={this.state.currAlbum}
              photos={this.state.photos}
		      setAlbumCallBack={this.setAlbumCallBack}
            />
			</div>
		    <div className="col m1 l3">
			</div>
			</div>
          </div>
        ) : null}
        {this.state.currentTab === Tab.HOME ? <Home /> : null}
        {this.state.currentTab === Tab.COOKING ? <Cooking idtoken={this.state.idtoken} triggerEdit={this.triggerEdit} key={this.state.forceKey} /> : null}
		<EditRecipe recipeToEdit={this.state.recipeToEdit} idtoken={this.state.idtoken} recipeName={this.state.recipeName} recipeEditCallback={this.recipeEditCallback}/>
		<AddPhotosBulk albumsList={this.state.albumsList} idtoken={this.state.idtoken} setAlbumCallBack={this.setAlbumCallBack} />
      </div>
    );
  }
}

export default App;

