import React from "react";
import M from "materialize-css";
import GoogleLogin from "react-google-login";
import { GoogleLogout } from "react-google-login";
import AddAlbum from "./AddAlbum.js";
import DisplayAlbum from "./DisplayAlbum.js";
import DeleteAlbum from "./DeleteAlbum.js";
import logo from "./images/default.png";
import office from "./images/office.jpg";
import axios from "axios";

const modalOptions = {
  opacity: 0,
};

class NavBar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoggedIn: false,
      profilePicture: logo,
      name: "",
      email: "",
      addAlbumModal: null,
      addPhotoModal: null,
	  deleteModal: null,
	  displayModal: null,
      token: "",
      albums: [],
      albumsForDisplay: [],
    };

    this.createAlbumRef = React.createRef();
    this.deleteAlbumRef = React.createRef();
    this.displayAlbumRef = React.createRef();
    //this.addPhotosRef = React.createRef();
	this.addPhotosBulkRef = React.createRef();
  }

  closeModals = () => {
    this.state.addAlbumModal.close();
    this.state.deleteModal.close();
	this.state.displayModal.close();
  };

  componentDidMount() {
	// Weird bug ?
	if (!this.state.isLoggedIn) {
      this.displayAlbumRef.current.classList.add("disabled");
      this.deleteAlbumRef.current.classList.add("disabled");
      this.createAlbumRef.current.classList.add("disabled");
      //this.addPhotosRef.current.classList.add("disabled");
      this.addPhotosBulkRef.current.classList.add("disabled");
	}

    const elem2 = document.getElementById("modal2");
    const modal = M.Modal.init(elem2, modalOptions);

    const elem3 = document.getElementById("modal3");
    const modal2 = M.Modal.init(elem3, modalOptions);

	const elem4 = document.getElementById("display-albums");
	const modal3 = M.Modal.init(elem4, modalOptions);

	const elem5 = document.getElementById("delete-modal");
	const modal4 = M.Modal.init(elem5, modalOptions);

    //const elems = document.getElementById("display");
    //M.Dropdown.init(elems, dropdownOptions);

    this.setState({
      addAlbumModal: modal,
      addPhotoModal: modal2,
	  displayModal: modal3,
	  deleteModal: modal4
    });

    const options = {
      onCloseStart: this.closeModals,
    };

    const elem = document.querySelectorAll(".sidenav");
    M.Sidenav.init(elem, options);
  }

  handleClickAddAlbum = () => {
    this.state.addAlbumModal.open();
  };

  handleClickAddPhoto = () => {
    this.state.addPhotoModal.open();

    axios
      .get("/albums", {
        params: { idtoken: this.state.token, email: this.state.email },
      })
      .then((resolve) => {
        //console.log(resolve);
        this.setState({ albums: resolve.data });
      });
  };

  handleBulk = async () => {
    await axios
      .get("/albums", {
        params: { idtoken: this.state.token, email: this.state.email },
      })
      .then((resolve) => {
        //this.setState({ albums: resolve.data });
		this.props.triggerBulk(resolve.data);
      });

	// Testing
	//const test = ["Memories", "Default"];

	//this.props.triggerBulk(test);
  };

  /* Old method for display
  handleClickDisplayAlbum = (e, albumName) => {
	this.state.displayModal.open();	
    //this.props.setAlbumCallBack(albumName);
  };
  */

  handleDeleteAlbum = albumName => {
    this.state.deleteModal.open();
  };

  handleSetAlbum = albumName => {
	this.props.setAlbumCallBack(albumName);
  };

  handleDisplayAlbum = async () => {
    await axios
      .get("/albums", {
        params: { idtoken: this.state.token, email: this.state.email },
      })
      .then((resolve) => {
        this.setState({ albums: resolve.data });
      });

	this.state.displayModal.open();
  };

  onSuccess = async (response) => {
    const googleUser = response.getBasicProfile();
    const profile = googleUser.getImageUrl();
    const name = googleUser.getName();
    const email = googleUser.getEmail();

    this.setState({
      isLoggedIn: !this.state.isLoggedIn,
      profilePicture: profile,
      name: name,
      email: email,
      token: response.getAuthResponse().id_token,
    });

    this.createAlbumRef.current.classList.remove("disabled");
    this.deleteAlbumRef.current.classList.remove("disabled");
    this.displayAlbumRef.current.classList.remove("disabled");
    //this.addPhotosRef.current.classList.remove("disabled");
    this.addPhotosBulkRef.current.classList.remove("disabled");

	// Returns auth information to App
    this.props.getAuth({
      idtoken: this.state.token,
      email: this.state.email,
	  googleUser: response
    });

    // Load albums once user is authenticated
    await axios
      .get("/albums", {
        params: { idtoken: this.state.token, email: this.state.email },
      })
      .then((resolve) => {
        this.setState({ albumsForDisplay: resolve.data });
      });
  };

  onFailure = (response) => {
    console.log(response);
  };

  logout = () => {
    this.setState({
      isLoggedIn: !this.state.isLoggedIn,
      profilePicture: logo,
      name: "",
      email: "",
    });

	this.props.getAuth({
	  idtoken: "",
	  email: "",
	  googleUser: null
	});

    this.createAlbumRef.current.classList.add("disabled");
    this.deleteAlbumRef.current.classList.add("disabled");
    this.displayAlbumRef.current.classList.add("disabled");
    //this.addPhotosRef.current.classList.add("disabled");
    this.addPhotosBulkRef.current.classList.add("disabled");
    this.props.removeAuthCallBack();
  };

  updateListCallBack = () => {
    // Load albums if updated list of albums
    axios
      .get("/albums", {
        params: { idtoken: this.state.token, email: this.state.email },
      })
      .then((resolve) => {
        this.setState({ albumsForDisplay: resolve.data });
      });
  };

  render() {
    return (
      <div>
        <nav className="nav-extended">
          <div className="nav-wrapper">
            <a
              href="#!"
              className="logo paris"
              onClick={this.handleHome}
              style={{ cursor: "pointer" }}
            >
              Stiny
            </a>
            <a
              href="#!"
              data-target="slide-out"
              className="sidenav-trigger left waves-effect"
            >
              <i
                style={{ fontSize: "300%", color: "black" }}
                className="material-icons"
              >
                menu
              </i>
            </a>
          </div>
        </nav>
        <ul id="slide-out" className="sidenav">
          <li>
            <div className="user-view">
              <div className="background">
                <img alt="office-background" src={office} />
              </div>
              <a href="#user">
                <img
                  alt="profile"
                  className="circle"
                  src={this.state.profilePicture}
                />
              </a>
              <a href="#name">
                <span className="white-text name">{this.state.name}</span>
              </a>
              <a href="#email">
                <span className="white-text email">{this.state.email}</span>
              </a>
            </div>
          </li>
          <li>
            <a
              ref={this.createAlbumRef}
              className="waves-effect"
              href="#!"
              disabled={!this.state.isLoggedIn}
              onClick={this.handleClickAddAlbum}
            >
              <i className="material-icons">book</i>Create Album
            </a>
            <AddAlbum
              token={this.state.token}
              email={this.state.email}
              updateList={this.updateListCallBack}
            />
          </li>
		  <li>
            <a
			  onClick={this.handleDeleteAlbum}
              ref={this.deleteAlbumRef}
              className="waves-effect"
              href="#!"
            >
              <i className="material-icons">delete</i>Delete Album
            </a>
			<DeleteAlbum 
              token={this.state.token}
              updateList={this.updateListCallBack}
			/>
          </li>
          <li>
            <a
              ref={this.displayAlbumRef}
			  onClick={this.handleDisplayAlbum}
              className="dropdown-trigger waves-effect"
              data-target="dropdownDisplay"
			  id="display"
              href="#!"
              disabled={!this.state.isLoggedIn}
            >
              <i className="material-icons">tv</i>Display Album
            </a>
			<DisplayAlbum
			  token={this.state.token}
			  albums={this.state.albums}
			  handleSetAlbum={this.handleSetAlbum}
			/>
          </li>
          <li>
            <a
			  ref={this.addPhotosBulkRef}
              className="waves-effect"
              href="#!"
              onClick={this.handleBulk}
            >
              <i className="material-icons">insert_photo</i>Add Photos
            </a>
          </li>
          <li>
            <div className="divider"></div>
          </li>
          <li>
            <GoogleLogin
              className="google-button"
              clientId={process.env.REACT_APP_CLIENT_ID}
              buttonText="Login"
              onSuccess={this.onSuccess}
              onFailure={this.onFailure}
              cookiePolicy={"single_host_origin"}
              disabled={this.state.isLoggedIn}
			  isSignedIn={true}
            />
            <GoogleLogout
              className="google-button"
              clientId={process.env.REACT_APP_CLIENT_ID}
              buttonText="Logout"
              onLogoutSuccess={this.logout}
              disabled={!this.state.isLoggedIn}
            ></GoogleLogout>
          </li>
          <li>
            <a href="#!" className="subheader">
              Built With Materialize CSS
            </a>
          </li>
        </ul>
      </div>
    );
  }
}

