import React from "react";
import axios from "axios";
import { DEFAULT_ALBUM } from "./App.js";
import M from "materialize-css";
import EditPhoto from "./EditPhoto.js";

const options = {
  coverTrigger: false,
};

class ViewPicture extends React.Component {
  constructor(props) {
    super(props);
	this.state = {
	  picture: "",
	  caption: "",
 	  date: "",
	  editPhotoModal: null
	};

    this.myRef = React.createRef();
  }

  closeModal = () => {
	this.state.editPhotoModal.close();
  };

  componentDidUpdate(prevProps, prevState, snapshot) {
	if (this.state.caption === undefined && this.state.date === undefined) {
	  this.setState({
	    caption: prevState.caption,
		date: prevState.date
	  });
	}

    this.myRef.current.scrollTo(0, 0);
    let elems = document.querySelectorAll(".delete-pic");
    M.Dropdown.init(elems, options);
  }

  componentDidMount() {
    let elems = document.querySelectorAll(".delete-pic");
    M.Dropdown.init(elems, options);
  }

  handleEdit = async () => {
    const elem = document.getElementById("modal-edit-photo");
    const modal = M.Modal.init(elem);

	this.setState({
	  editPhotoModal: modal
	});

	modal.open();
  };

  handleDelete = () => {
    if (this.props.album !== undefined && this.props.album !== DEFAULT_ALBUM) {
      const request = {
        albumName: this.props.album,
        link: this.state.picture,
        idtoken: this.props.token,
      };

	  //console.log(request);

      axios.post("/deletePhoto", request).then(
        (response) => {
          //console.log("response: " );
          //console.log(response);
          this.props.deleteCallBack();
        },
        (error) => {
          //console.log("error");
          console.log(error);
          // TODO
        }
      );
    }
  };

  handleOnEdit = async (newDate, newCaption) => {
    // Load albums when a photo was updated
	// This goes to PhotoCarousel and then to App, which updates the photos for the album

	await this.state.editPhotoModal.close();
	this.props.handleOnEdit(newDate, newCaption);
  }

  render() {
    return (
      <div
        ref={this.myRef}
        id="modal1"
        className="modal"
        style={{
          width: "75%",
          maxHeight: "100%",
          backgroundColor: "rgba(255, 255, 255, 0.90)",
          position: "absolute",
        }}
      >
        <EditPhoto 
		  closeModal={this.closeModal} 
		  idtoken={this.props.token}
		  caption={this.state.caption} 
		  date={this.state.date} 
		  link={this.state.picture}
		  albumName={this.props.album}
		  handleOnEdit={this.handleOnEdit}
		  />
        <div className="modal-content" style={{}}>
          <div className="row">
            {this.props.album !== DEFAULT_ALBUM ? (
              <div>
                <a
				  onClick={this.handleEdit} 
				  href="#!" 
				  className="left waves-effect btn"
				>
                  Edit
                </a>
                <a
                  className="dropdown-trigger btn left red delete-pic"
                  href="#!"
                  data-target="dropdown1"
                >
                  Delete
                </a>
                <ul id="dropdown1" className="dropdown-content">
                  <li>
                    <a href="#!" onClick={this.handleDelete}>
                      Confirm
                    </a>
                  </li>
                </ul>
              </div>
            ) : null}
            <a
              href="#!"
              className="right modal-close waves-effect waves-green btn"
            >
              Exit
            </a>
          </div>
          <h4>{this.state.caption}</h4>
          <p>{this.state.date}</p>
          <img
            alt="preview"
            style={{ width: "100%", height: "100%" }}
            src={this.state.picture}
          />
        </div>
      </div>
    );
  }
}

export default ViewPicture;

