import React from 'react';
import M from "materialize-css";

class MenuBar extends React.Component {
  componentDidMount() {
	let elem = document.querySelector('.tabs');
	M.Tabs.init(elem);

	this.photosRef = React.createRef();
  }

  handleClick = event => {
	this.props.menuBarCallBack(event.target.text);
  };

  render() {
	return (
	  <div className="row">
        <div className="col s12">
          <ul className="tabs">
        	<li className="tab col s4"><a onClick={this.handleClick} href="#cooking">Cooking</a></li>
            <li className="tab col s4"><a className="active" onClick={this.handleClick} href="#home">Home</a></li>
            <li className="tab col s4"><a onClick={this.handleClick} href="#photos" ref={this.photosRef} >Photos</a></li>
          </ul>
    	</div>
  	  </div>
	);
  }
}

export default MenuBar;

