import React from 'react';
import axios from "axios";

class EditPhoto extends React.Component {
  constructor(props) {
    super(props);

	this.editCaptionRef = React.createRef();
	this.editDateRef = React.createRef();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    this.editCaptionRef.current.value = this.props.caption;
	this.editDateRef.current.value = this.props.date;
  }

  handleSubmit = async () => {
	const newDate = this.editDateRef.current.value;
	const newCaption = this.editCaptionRef.current.value;

	const request = {
      albumName: this.props.albumName,
	  idtoken: this.props.idtoken,
	  link: this.props.link,
	  date: newDate,
	  caption: newCaption
	};

	const editProcedure = this.props.handleOnEdit;

 	await axios.post("/editPhoto", request)
		.then(resolve => {
		  // Update the album list data
		  editProcedure(newDate, newCaption); 
		}, error => {
		  console.log(error); 
		});
  };

  render() {
    return (
	  <div id="modal-edit-photo" className="modal arg">
        <div className="modal-content">
          <a
            href="#!"
            className="waves-effect"
            style={{ display: "inline-block" }}
			onClick={this.props.closeModal}
          >
            Close
          </a>

          <h4>Edit Photo Descriptions</h4>
		 
		  <div className="row">
            <div className="input-field col s12">
              <input ref={this.editCaptionRef} placeholder="Trip to the beach!" id="edit-caption" type="text"/>
              <label htmlFor="edit-caption" className="active">Caption</label>
            </div>
          </div>

		  <div className="row">
            <div className="input-field col s12">
              <input ref={this.editDateRef} placeholder="12/08/2018" id="edit-date" type="text"/>
              <label htmlFor="edit-date" className="active">Date</label>
            </div>
          </div>

          <a
            href="#!"
            onClick={this.handleSubmit}
            className="waves-effect waves-light btn"
          >
            <i className="material-icons left">create</i>Submit
          </a>

        </div>
      </div>  
	);
  }
}

export default EditPhoto;
